const connection = require('./config/database');

const debugQuery = `
SELECT r.id_reservasi, r.status, p.metode_pembayaran, p.jumlah, p.status_pembayaran
FROM reservasi r
LEFT JOIN pembayaran p ON p.id_reservasi = r.id_reservasi
WHERE r.status = 'Selesai'
ORDER BY r.id_reservasi DESC;
`;

connection.query(debugQuery, (err, results) => {
  if (err) {
    console.error('Error:', err);
    return;
  }

  console.log('Results for Selesai reservations:');
  console.table(results);

  // Also check all pembayaran records
  const allPaymentsQuery = `
    SELECT * FROM pembayaran
    WHERE id_reservasi IN (
      SELECT id_reservasi FROM reservasi WHERE status = 'Selesai'
    )
    ORDER BY id_reservasi DESC;
  `;

  connection.query(allPaymentsQuery, (err2, payments) => {
    if (err2) {
      console.error('Error getting payments:', err2);
      return;
    }

    console.log('\nAll pembayaran records for Selesai reservations:');
    console.table(payments);

    connection.end();
  });
});
